:root {
  --color-surface: white;
  --color-solid: black;
  --color-primary: teal;
}


html {
  background: var(--color-surface);
  color: var(--color-solid);
}
button {
  background: var(--color-surface);
  color: var(--color-primary);
  border-color: var(--color-primary);
  padding: 8px 16px;
  font-size: 1.2rem;
  border-radius: 4px;
}
.beaker {
  position: absolute;
  animation: x 0.5s infinite;
  -webkit-animation: x 0.5s infinite;
  -moz-animation: x 0.5s infinite;
  -o-animation: x 0.5s infinite;
}

